        "description": "Lesti - Kulepas Dengan Ikhlas | Official Music Video\n\n#Lesti #LestiBillar #MusicVideo #Lagubarulesti #laguadibal #videodangdutterbaru\n\nSelepas merilis lagu bertemakan patah hati yang mendalam beberapa bulan lalu, kini Lesti kembali melahirkan sebuah karya baru dengan tema 'Keikhlasan'.  Dibawah naungan label rekaman 3D Entertainment dan manajemen artis STREAM Entertainment, single berjudul 'Kulepas Dengan Ikhlas' dirilis tepat dihari ulang tahun Lesti, 5 Agustus 2020, melalui music video di channel youtube 3D Entertainment.\n\nSetelah sebelumnya kisah Lesti dikait-kaitkan dengan aktor Rizky Billar, yang memiliki background kisah cinta yang sama, dalam music video Lesti terbaru pun, keduanya dipertemukan. \n\nAdegan dalam music video tersebut sangat berkaitan dengan lirik yang diciptakan Adibal, penuh keikhlasan. Berangkat dari rasa simpati yang mendalam dan kedekatan secara pribadi, secara khusus Adibal Sahrul menciptakan lagu untuk Lesti.  \"Lagu ini saya tulis tepat tanggal 17 Juli saat melihat postingan Lesti menghadiri pernikahan Rizky DA, cukup cepat bikinnya 1 jam aja... mungkin karena saya cukup dekat dengan mereka berdua, jadi saya dapat meraba apa yang mungkin mereka rasakan, khususnya Lesti, Kesan saya , lesti itu punya hati yg kuat ,apapun alasannya , gak mudah pastinya berada dalam situasi itu,\" tutur Adibal.\n\nLagu ini terasa paket lengkap dari sisi lirik, balutan musik yang menyentuh hati dan diperankan langsung oleh sosok yang mengalami kisahnya dan sebuah music video.  Semoga pesan besar dalam lagu ini dapat tersampaikan yaitu kita boleh merencanakan apa saja, pada akhirnya Tuhan yang menentukan.\n\nComposed by ADIBAL SAHRUL\nPublished by MAHAR PUSTAKA NUSANTARA\nProduced by ADIBALMUSIC PRODUCTION\nArranged by YUSUP TOJIRI\nKeys performed by YUSUP TOJIRI\nGuitars performed by VITO\nGendang performed by DODI\nBass performed by ACHO\nSuling 1 performed by TIKNO\nSuling 2 & Flute performed by ANSUL\nVocal Produced by UAK ASEP & ADIBAL \nSong Recorded @ADIBALMUSICSTUDIO\nMixed & Mastered by TIRTA\nTRINITY OPTIMA PRODUCTION 2020\n\n====================================\n\nMusic Video\n\nDirector: Alif Patterson \nAssistant Director: Finny VSLZM\nProducer: Anthony VSLZM\nAssistant Producer: Michael VSLZM\n\nDoP: Ralf Morscheck\n1st Assistant Camera: Anjar Prabowo\n2nd Assistant Camera: Alvin Lee\n\nGaffer: Ryan Sandi Gunawan\nLighting: Syto Bagas Pangestu & Nur Diansyah Klowor\n\nArt: Finny VSLZM, Anthony VSLZM, & Maximillian VSLZM\nLocation Manager: Anthony VSLZM\n\nOffline Editor: Maximillian VSLZM & Finny VSLZM\nOnline Editor: Sweetpud & Michael VSLZM\n\nMake Up Artist : Rizky Make Up\nHair Do Rizky Billar : Budi\nStylist : Elco Febrian & Andra Astari\n\n====================================\n\nLagu Kulepas Dengan Ikhlas akan segera dirilis di aplikasi musik streaming favoritmu, simpan dulu lagunya klik https://lnk.to/kulepasdenganikhlas \n\n-----------------------------------------------\nYuk Follow!\n\nInstagram : https://instagram.com/3dent_id\nTwitter : https://twitter.com/3dent_id\nFacebook : https://www.facebook.com/3dentertainm...\nBeauty Line Lesti : https://instagram.com/purnamabeautyofficial\n\n3D Entertainment adalah sub-label dari PT. Trinity Optima Production (TOP) yang menaungi artis-artis dengan segmen musik Dangdut.",
